import React, { useState } from 'react';
import './TodoList.scss';

type Props = {
  todos: Todo[],
  setSelectedUserId: (arg0: number) => void,
};

enum Status {
  Active = 'Active',
  Completed = 'Completed',
  All = 'All',
}

export const TodoList: React.FC<Props> = ({ todos, setSelectedUserId }) => {
  const [query, setQuery] = useState('');
  const [completed, setCompleted] = useState('');

  const isFiltered = () => {
    if (completed === Status.Active) {
      return todos.filter(todo => (
        todo.title.toLowerCase().startsWith(query.toLowerCase())
        && todo.completed === false
      ));
    }

    if (completed === Status.Completed) {
      return todos.filter(todo => (
        todo.title.toLowerCase().startsWith(query.toLowerCase())
        && todo.completed === true
      ));
    }

    return todos.filter(todo => (
      todo.title.toLowerCase().startsWith(query.toLowerCase())
    ));
  };

  const filtered = isFiltered();

  return (
    <div className="TodoList">
      <h2>Todos:</h2>

      <div className="TodoList__list-container">
        <form action="">
          <input
            type="text"
            data-cy="filterByTitle"
            value={query}
            onChange={(event) => {
              setQuery(event.target.value);
            }}
          />
          <select
            onChange={(event) => {
              setCompleted(event.target.value);
            }}
          >
            <option value={Status.All}>All</option>
            <option value={Status.Active}>Active</option>
            <option value={Status.Completed}>Completed</option>
          </select>
        </form>
        <ul
          className="TodoList__list"
          data-cy="listOfTodos"
        >
          {
            filtered.map(todo => (
              <li className={`TodoList__item TodoList__item--${todo.completed}`} key={todo.id}>
                <label>
                  <input
                    type="checkbox"
                    defaultChecked={todo.completed}
                    disabled
                  />
                  <p>{todo.title}</p>
                </label>

                {todo.userId
                  && (
                    <button
                      data-cy="userButton"
                      className="
                      TodoList__user-button
                      TodoList__user-button--selected
                      button
                    "
                      type="button"
                      onClick={() => setSelectedUserId(todo.userId)}
                    >
                      {`User#${todo.userId}`}
                    </button>
                  )}
              </li>
            ))
          }
        </ul>
      </div>
    </div>
  );
};
